<template>
    <div class="back h-screen pb-8 xl:pb-8 md:pb-10">
        <div class="container mx-auto">
            <div class="flex flex-row justify-between mx-4 py-8">
                <div>
                    <a class="text-blue-500" href="/">IMEDICAL</a>
                </div>
                <div>
                    <h4 class="text-white">Заявка на врача</h4>
                </div>
                <div>
                    <a class="text-blue-500" @click="logout()">Выйти</a>
                </div>
            </div>
            <div class="mx-4 mt-10">
                <ul class="rounded-lg back__li px-5 py-2">
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>-Имя</h5>
                            <input v-model="name" class="w-full" placeholder="Имя" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Фамилия</h5>
                            <input v-model="family" class="w-full" placeholder="Полвонбоев" type="text">
                        </div>
                    </li>
                    <li class="flex flex-row justify-between text-white back__input xl:text-xl text-base md:text-xl py-2">
                        <div class="flex flex-col">
                            <span>Год рождения</span>
                            <date-picker class="mt-4 border__bottom w-full text-white" v-model="date" valueType="format"></date-picker>
                        </div>
                    </li>
                    <li class="flex flex-row justify-between text-white xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full">
                            <label class="block text-left border__bottom w-full ">
                                <span class="text-white">Списком</span>
                                <select v-model="list" class="form-select py-2 -ml-1 block w-full outline-none mt-4 back__li">
                                    <option>Аллерголог</option>
                                    <option>Вирусолог</option>
                                    <option>Гастроэнтеролог</option>
                                    <option>Дерматолог</option>
                                    <option>Кардиолог</option>
                                    <option>ЛОР</option>
                                    <option>Невропатолог</option>
                                    <option>Стоматолог</option>
                                    <option>Терапевт</option>
                                    <option>Эндокринолог</option>
                                </select>
                            </label>
                        </div>
                    </li>
                    <li class="flex flex-row justify-between text-white xl:text-xl text-base md:text-xl pt-2 pb-8">
                        <div class="w-full input__change">
                            <h5>Номер телефона</h5>
                            <input v-model="number" class="w-full" placeholder="+998977654321" type="tel">
                        </div>
                    </li>
                   
                </ul>
            </div>
            <div class="flex justify-center mt-12">
                <button @click="submitHandler()" class="p-2 pl-5 pr-5 bg-green-500 text-gray-100 text-lg rounded-lg focus:border-4 border-green-300">Отправить</button>
            </div>
        </div>
    </div>
</template>

<script>
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

export default {
    data: () => ({
        selectedFile: null,
        name: '',
        family: '',
        list: '',
        number: '',
        date: null,
    }),
    methods: {
    async submitHandler() {
        const info = {
            name: this.name,
            family: this.family,
            list: this.list,
            number: this.number,
            date: this.date
        }
        await this.$store.dispatch('registerForDoctor', info)
        this.name = ''
        this.family = ''
        this.list = '',
        this.number = ''
        this.$toasted.show("Отправлено успешно!", { 
            theme: "outline", 
            position: "top-right", 
            duration : 3000
        });
    },
    async logout() {
        await this.$store.dispatch('logout')
        this.$router.go('/')
    }
    },
    components: {
        DatePicker 
    }
}
</script>

<style>
    .back {
        background: #1c1c1e;
    }
    .back__li {
        background: #2c2c2e;
    }
    .border__li {
        border-bottom: 1px solid #878a8a;
    }
    .border__bottom {
        border-bottom: 1px solid #878a8a;
    }
    .vdp-datepicker__calendar {
        background: #1c1c1e !important
    }
    .vdp-datepicker__calendar header .prev:not(.disabled):hover, .vdp-datepicker__calendar header .next:not(.disabled):hover, .vdp-datepicker__calendar header .up:not(.disabled):hover {
        background: #2c2c2e !important;
    }
    .back__input input {
        background: #2c2c2e;
        outline: none;
        padding-bottom: 5px;
    }
    .input__change input {
        background: #2c2c2e;
        border-bottom: 1px solid #878a8a;
        outline: none;
        padding-bottom: 5px;
        color: #ffffff;
    }
    .input__change h5 {
        margin-top: 20px;
        margin-bottom: 15px;
        color: #ffff
    }
    .img-input {
        display: none;
    }
    .mx-input {
        color: #fff;
    }
</style>