<template>
    <div class="back h-full pb-8 xl:pb-8 md:pb-10">
        <div class="container mx-auto">
            <div class="flex flex-row justify-between mx-4 py-8">
                <div>
                    <a class="text-blue-500" href="/">IMEDICAL</a>
                </div>
                <div>
                    <a class="text-blue-500" href="/list">Заявки</a>
                </div>
                <div>
                    <h4 class="text-white">Регистрация пользователя</h4>
                </div>
                <div>
                    <a class="text-blue-500" href="/listurl">Пользователи</a>
                </div>
                
                <div>
                    <a class="text-blue-500 cursor-pointer" @click="logout()">Выйти</a>
                </div>
            </div>
            <div>
                <img :src="selectedFile" class="w-24 rounded-full h-24 object-cover mx-auto bg-gray-700" alt="">
                <div class="flex justify-center mt-8">
                    <label for="file-input" class="p-2 pl-5 pr-5 bg-blue-500 text-gray-100 text-lg rounded-lg focus:border-4 border-blue-300 mx-auto">Добавить фото</label>
                    <input type="file" id="file-input" class="img-input" @change="onFileSelected">
                </div>
            </div>
            <div class="mx-4 mt-10">
                <ul class="rounded-lg back__li px-5 py-2">
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>-Имя</h5>
                            <input v-model="name" class="w-full" placeholder="Имя" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Фамилия</h5>
                            <input v-model="family" class="w-full" placeholder="Полвонбоев" type="text">
                        </div>
                    </li>
                    <li class="flex flex-row justify-between text-white back__input xl:text-xl text-base md:text-xl py-2">
                        <div class="flex flex-col">
                            <span>Год рождения</span>
                            <date-picker class="mt-4 border__bottom w-full text-white" v-model="date" valueType="format"></date-picker>
                        </div>
                    </li>
                    <li class="flex flex-row justify-between text-white xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full">
                            <label class="block text-left border__bottom w-full ">
                                <span class="text-white">Пол</span>
                                <select v-model="gender" class="form-select py-2 -ml-1 block w-full outline-none mt-4 back__li">
                                    <option>Мужчина</option>
                                    <option>Женщина</option>
                                </select>
                            </label>
                        </div>
                    </li>
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>Группа крови</h5>
                            <input v-model="blood" placeholder="Группа крови" class="w-full" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Заболевания</h5>
                            <input v-model="diseases" placeholder="Заболевания" class="w-full" type="text">
                        </div>
                    </li>
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>Аллергические реакции</h5>
                            <input v-model="allergic" placeholder="Аллергические реакции" class="w-full" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Медикаменты</h5>
                            <input v-model="medication" placeholder="Медикаменты" class="w-full" type="text">
                        </div>
                    </li>
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl pb-8">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>Вес</h5>
                            <input v-model="weight" placeholder="Вес" class="w-full" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Рост</h5>
                            <input v-model="height" placeholder="Рост" class="w-full" type="text">
                        </div>
                    </li>
                </ul>
            </div>
            <div class="flex justify-center mt-12">
                <button @click="submitHandler()" class="p-2 pl-5 pr-5 bg-green-500 text-gray-100 text-lg rounded-lg focus:border-4 border-green-300">Сохранить</button>
            </div>
        </div>
    </div>
</template>

<script>
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import firebase from 'firebase'
export default {
    data: () => ({
        selectedFile: null,
        name: '',
        family: '',
        date: new Date(2021, 5,  5),
        blood: '',
        weight: '',
        height: '',
        diseases: '',
        allergic: '',
        gender: '',
        medication: '',
    }),
    methods: {
    onFileSelected(event) {
        const file = event.target.files[0]
        var storageRef = firebase.storage().ref(`users/${this.uid}/` + file.name);
        this.loading = true
        let uploadTask = storageRef.put(file);
        uploadTask.on('state_changed', (snapshot) =>{
            console.log(snapshot)
        }, (error) => {
            console.log(error)
        }, () => {
            uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
            this.selectedFile = downloadURL
            this.loading = false
            })
        })
    },
    async submitHandler() {
        const info = {
            name: this.name,
            family: this.family,
            date: this.date,
            blood: this.blood,
            weight: this.weight,
            height: this.height,
            diseases: this.diseases,
            medication: this.medication,
            allergic: this.allergic,
            gender: this.gender,
            photo: this.selectedFile
        }
        await this.$store.dispatch('registerProfile', info)
        this.$toasted.show("Успешно зарегистрировано!", { 
            theme: "outline", 
            position: "top-right", 
            duration : 3000
        });
    },
    async logout() {
        await this.$store.dispatch('logout')
        this.$router.go('/')
    }
    },
    components: {
        DatePicker
    }
}
</script>

<style>
    .back {
        background: #1c1c1e;
    }
    .back__li {
        background: #2c2c2e;
    }
    .border__li {
        border-bottom: 1px solid #878a8a;
    }
    .border__bottom {
        border-bottom: 1px solid #878a8a;
    }
    .vdp-datepicker__calendar {
        background: #1c1c1e !important
    }
    .vdp-datepicker__calendar header .prev:not(.disabled):hover, .vdp-datepicker__calendar header .next:not(.disabled):hover, .vdp-datepicker__calendar header .up:not(.disabled):hover {
        background: #2c2c2e !important;
    }
    .back__input input {
        background: #2c2c2e;
        outline: none;
        padding-bottom: 5px;
    }
    .input__change input {
        background: #2c2c2e;
        border-bottom: 1px solid #878a8a;
        outline: none;
        padding-bottom: 5px;
        color: #ffffff;
    }
    .input__change h5 {
        margin-top: 20px;
        margin-bottom: 15px;
        color: #ffff
    }
    .img-input {
        display: none;
    }
</style>