import firebase from 'firebase/app'

export default {
  state: {
    error: null,
    lists: []
  },
  mutations: {
    loadProducts(state, item) {
      state.lists = item
    },
    setError(state, error) {
      state.error = error
    },
  },
  getters: {
    error: s => s.error,
  },
  actions: {
    async login({commit}, {email, password}) {
      try {
        await firebase.auth().signInWithEmailAndPassword(email, password)
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async register({dispatch, commit}, {email, password, date}) {
      try {
        await firebase.auth().createUserWithEmailAndPassword(email, password)
        const uid = await dispatch('getUid')
        await firebase.database().ref(`/users/${uid}/info`).set({
          date,
          email
        })
        console.log(uid);
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async registerProfile({commit}, {name, family, date, blood, weight, height, diseases, medication, allergic, gender, photo}) {
      try {
        const info = await firebase.database().ref(`/users/`).push({name, family, date, blood, weight, height, diseases, medication, allergic, gender, photo})
        return {name, family, date, blood, weight, height, diseases, medication, allergic, gender, photo, id: info.key}
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async registerForDoctor({commit}, {name, family, list, number, date}) {
      try {
        const info = await firebase.database().ref(`/apply/`).push({name, family, list, number, date})
        return {name, family, list, number, date, id: info.key}
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    async fetchList({commit}) {
        const lists = (await firebase.database().ref(`/apply/`).once('value')).val()
        const cats = []
        Object.keys(lists).forEach(key => {
          cats.push({
            name: lists[key].name,
            family: lists[key].family,
            number: lists[key].number,
            list: lists[key].list,
            id: key
          })
          commit('loadProducts', lists)
        })
        return cats  
    },
    async fetchShoesById({commit}, id) {
      try{
        const shoe = (await firebase.database().ref(`/users`).child(id).once('value')).val() || {}
        return {...shoe, id}
      } catch(e) {
        commit('setError', e)
        throw e
      }
    },
    async fetchUser({commit}) {
      try {
        const comment = (await firebase.database().ref('/users').once('value')).val() || {}
        return Object.keys(comment).map(key => ({...comment[key], id: key}))
      } catch (e) {
        commit('setError', e)
        throw e
      } 
    },
    getUid() {
      const user = firebase.auth().currentUser
      return user ? user.uid : null
    },
    async logout({commit}) {
      await firebase.auth().signOut()
      await commit('clearInfo')
    },
  }
}