<template>
    <div class="back h-full pb-8 xl:pb-8 md:pb-10">
        <div class="container mx-auto">
            <div class="flex flex-row justify-between mx-4 py-8">
                <div>
                    <a class="text-blue-500" href="#">IMEDICAL</a>
                </div>
                <div>
                    <h4 class="text-white">Изменить</h4>
                </div>
                <div>
                    <a class="text-blue-500" href="#">Узнать URL</a>
                </div>
            </div>
            <div>
                <img :src="selectedFile" class="w-24 rounded-full h-24 object-cover mx-auto" alt="">
                <div class="flex justify-center mt-8">
                    <label for="file-input" class="p-2 pl-5 pr-5 bg-blue-500 text-gray-100 text-lg rounded-lg focus:border-4 border-blue-300 mx-auto">Добавить фото</label>
                    <input type="file" id="file-input" class="img-input" @change="onFileSelected">
                </div>
            </div>
            <div class="mx-4 mt-10">
                <ul class="rounded-lg back__li px-5 py-2">
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>-Имя</h5>
                            <input class="w-full" placeholder="Имя" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Фамилия</h5>
                            <input class="w-full" placeholder="Полвонбоев" type="text">
                        </div>
                    </li>
                    <li class="flex flex-row justify-between text-white back__input xl:text-xl text-base md:text-xl py-2">
                        <datepicker class="mt-4 border__bottom w-full" placeholder="Выберите дату" v-model="vModelExample"></datepicker>
                    </li>
                    <li class="flex flex-row justify-between text-white xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full">
                            <label class="block text-left border__bottom w-full ">
                                <span class="text-white">Пол</span>
                                <select class="form-select py-2 -ml-1 block w-full outline-none mt-4 back__li">
                                    <option>Мужчина</option>
                                    <option>Женщина</option>
                                </select>
                            </label>
                        </div>
                    </li>
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>Группа крови</h5>
                            <input placeholder="Группа крови" class="w-full" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Заболевания</h5>
                            <input placeholder="Заболевания" class="w-full" type="text">
                        </div>
                    </li>
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>Аллергические реакции</h5>
                            <input placeholder="Аллергические реакции" class="w-full" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Медикаменты</h5>
                            <input placeholder="Медикаменты" class="w-full" type="text">
                        </div>
                    </li>
                    <li class="flex xl:flex-row md:flex-row flex-col justify-between text-gray-900 xl:text-xl text-base md:text-xl py-2">
                        <div class="w-full xl:mr-2 md:mr-2 mr-0 input__change">
                            <h5>Вес</h5>
                            <input placeholder="Вес" class="w-full" type="text">
                        </div>
                        <div class="w-full xl:ml-2 md:ml-2 ml-0 input__change">
                            <h5>Рост</h5>
                            <input placeholder="Рост" class="w-full" type="text">
                        </div>
                    </li>
                </ul>
            </div>
            <div class="flex justify-center mt-12">
                <button class="p-2 pl-5 pr-5 bg-green-500 text-gray-100 text-lg rounded-lg focus:border-4 border-green-300">Сохранить</button>
            </div>
        </div>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
import firebase from 'firebase'
export default {
    data: () => ({
        selectedFile: null,
        name: '',
        uid: '',
        userInfo: '',
        loading: false
    }),
    methods: {
    async onUpload() {
        if(this.$v.$invalid) {
            this.$v.$touch()
            return
        }
        const Info = {
            name: this.name,
            photo: this.selectedFile,
            userInfo: this.userInfo
        }
        await this.$store.dispatch('updatePhoto', Info);
      
    },
    onFileSelected(event) {
        const file = event.target.files[0]
        var storageRef = firebase.storage().ref(`users/${this.uid}/` + file.name);
        this.loading = true
        let uploadTask = storageRef.put(file);
        uploadTask.on('state_changed', (snapshot) =>{
            console.log(snapshot)
        }, (error) => {
            console.log(error)
        }, () => {
            uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
            this.selectedFile = downloadURL
            console.log(downloadURL)
            this.loading = false
            })
        })
    },
    nextPage() {
        if(this.$v.$invalid) {
            this.$v.$touch()
            return
        }
        if(this.loading === false) {
            this.$router.push({ name: 'UserInfo' })
        } else {
            console.log("Wait")
        }     
        },
        saveInfo() {
        if(this.$v.$invalid) {
            this.$v.$touch()
            return
        }
        if(this.loading === false) {
            this.$router.push({ name: 'Edit' })
        } else {
            console.log("Wait")
        }     
        },
        backRoute() {
            this.$router.go(-1)
        },
    },
    components: {
        Datepicker
    }
}
</script>

<style>
    .back {
        background: #1c1c1e;
    }
    .back__li {
        background: #2c2c2e;
    }
    .border__li {
        border-bottom: 1px solid #878a8a;
    }
    .border__bottom {
        border-bottom: 1px solid #878a8a;
    }
    .vdp-datepicker__calendar {
        background: #1c1c1e !important
    }
    .vdp-datepicker__calendar header .prev:not(.disabled):hover, .vdp-datepicker__calendar header .next:not(.disabled):hover, .vdp-datepicker__calendar header .up:not(.disabled):hover {
        background: #2c2c2e !important;
    }
    .back__input input {
        background: #2c2c2e;
        outline: none;
        padding-bottom: 5px;
    }
    .input__change input {
        background: #2c2c2e;
        border-bottom: 1px solid #878a8a;
        outline: none;
        padding-bottom: 5px;
        color: #ffffff;
    }
    .input__change h5 {
        margin-top: 20px;
        margin-bottom: 15px;
        color: #ffff
    }
    .img-input {
        display: none;
    }
</style>