import Vue from "vue";
import App from "./App.vue";
import router from './router'
import store from './store'
import Toasted from 'vue-toasted';
 

import VModal from 'vue-js-modal/dist/index.nocss.js'
import 'vue-js-modal/dist/styles.css'
import Vuelidate from 'vuelidate'
// You need a specific loader for CSS files


Vue.use(Vuelidate)
Vue.use(VModal)
Vue.use(Toasted)


import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import 'firebase/storage'


Vue.config.productionTip = false

firebase.initializeApp({
  apiKey: "AIzaSyCpM6tY0FbdB9-o58Ar51LX_2qFs9aoqzk",
  authDomain: "imedical-service.firebaseapp.com",
  databaseURL: "https://imedical-service-default-rtdb.firebaseio.com",
  projectId: "imedical-service",
  storageBucket: "imedical-service.appspot.com",
  messagingSenderId: "245917737915",
  appId: "1:245917737915:web:f59fe271ac612181905a9e",
  measurementId: "G-QH7Q85WVXT"
});

let app

firebase.auth().onAuthStateChanged(() => {
  if(!app) {
    app = new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }
  
})