<template>
  <div>
    
    <modal  :width="600"
            :height="450"
            :adaptive="true" 
            name="modal">
      <div class="logo mt-8">
        <img src="../assets/logo.png" class="mx-auto" alt="">
      </div>
      <div class="w-full">
        <form @submit.prevent="submitHandler" class="bg-white px-8 pt-6 pb-8 mb-4">
          <div class="mb-4">
            <label class="block text-gray-700 text-lg mb-2" for="username">
              Логин
            </label>
            <input v-model="email" class="shadow appearance-none border w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="imedical@med.uz">
            <p v-if="$v.email.$dirty && !$v.email.required" class="text-red-500 text-xs italic">Пожалуйста, выберите email</p>
          </div>
          <div class="mb-6">
            <label class="block text-gray-700 text-lg mb-2" for="password">
              Пароль
            </label>
            <input v-model="password" type="password" class="shadow appearance-none border w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" placeholder="*******">
            <p v-if="$v.email.$dirty && !$v.email.required" class="text-red-500 text-xs italic">Пожалуйста, выберите пароль</p>
            <p v-show="notFound" class="text-red-500 text-xs italic">Пользователь не существует.</p>

          </div>
          <div class="flex items-center justify-between">
            <button type="submit" class="button-modal text-white text-lg py-2 px-6 focus:outline-none focus:shadow-outline">
              Войти
            </button>
            <a class="inline-block align-baseline font-bold text-sm forget-modal" href="https://t.me/protag1">
              Забыли пароль?
            </a>
          </div>
        </form>
        <p class="text-center text-gray-500 text-xs">
          &copy; 2021 IMEDICAL.UZ — Электронная регистрация.
        </p>
      </div>
    </modal>
  </div>
</template>
<script>
import { required, minLength } from 'vuelidate/lib/validators'
export default {
  name: 'MyComponent',
  props: ['logo'],
  data: () => ({
    email: '1',
    password: '1',
    notFound: false
  }),
  validations: {
    email: {
      required,
    },
    password: {
      required,
      minLength: minLength(6)
    }
  },
  
  methods: {
    show () {
      this.$modal.show('modal');
      
    },
    hide () {
      this.$modal.hide('modal');
    },
    async submitHandler() {
      if(this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      
      const formData = {
        email: this.email,
        password: this.password
      }
      try {
        await this.$store.dispatch('login', formData)
        this.$router.push('/register')
      } catch (e) {
        console.log(e)
        this.notFound = true
      }
      
    }
  },
  mount () {
    this.show()
  }
}
</script>
<style scoped>
  .logo img {
    width: 50px;
  }
  .button-modal {
    background: #60a6c2;
  }
  .forget-modal {
    color: #60a6c2;
  }
  .button-modal:hover {
    background: #50a2c2;
  }
  .forget-modal:hover {
    color: #50a2c2;
  }
</style>